import React, { useState, useEffect } from 'react';
import { useAuth } from '../../context/AuthContext';
import './UserEdit.css'
import QRCode from 'qrcode.react';
import InteractiveImage from './InteractiveImage';

const UserProfile = () => {
  const { currentUser } = useAuth();
  const [profile, setProfile] = useState({});

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const response = await fetch(`/api/users/${currentUser.uid}`);
        const data = await response.json();
        setProfile(data);
      } catch (error) {
        console.error(error);
      }
    };

    if (currentUser) {
      fetchProfile();
    }
  }, [currentUser]);

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold">{profile.name}</h1>
      <InteractiveImage imageUrl={profile.profilePhoto} />
      <img src={profile.coverPhoto} alt="Cover" className="mt-4 w-full h-auto" />
      <p className="mt-2">Email: {profile.email}</p>
      <p>Phone: {profile.phone}</p>
      <QRCode value={`${window.location.origin}/profile/${profile._id}`} className="mt-4" />
    </div>
  );
};

export default UserProfile;