import React, { useRef, useEffect } from 'react';

const InteractiveImage = ({ imageUrl }) => {
  const imageRef = useRef(null);

  useEffect(() => {
    const image = imageRef.current;
    const handleMouseMove = (e) => {
      const { left, top, width, height } = image.getBoundingClientRect();
      const x = (e.clientX - left) / width - 0.5;
      const y = (e.clientY - top) / height - 0.5;
      image.style.transform = `rotateX(${y * 30}deg) rotateY(${x * 30}deg)`;
    };

    image.addEventListener('mousemove', handleMouseMove);
    return () => image.removeEventListener('mousemove', handleMouseMove);
  }, []);

  return <img ref={imageRef} src={imageUrl} alt="Interactive" className="transition-transform transform-perspective" />;
};

export default InteractiveImage;