import React, { useState } from 'react';
import { signUpWithEmail } from '../../services/firebase';

const Signup = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await signUpWithEmail(email, password);
      // Handle success (e.g., redirect to login or profile page)
    } catch (error) {
      console.error("Error signing up: ", error);
      // Handle error (e.g., show error message to user)
    }
  };

  return (
    <form onSubmit={handleSubmit} className="p-4">
      <label>
        Name:
        <input type="text" value={name} onChange={(e) => setName(e.target.value)} className="border p-2" />
      </label>
      <br />
      <label>
        Email:
        <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} className="border p-2" />
      </label>
      <br />
      <label>
        Password:
        <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} className="border p-2" />
      </label>
      <br />
      <label>
        Phone Number:
        <input type="text" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} className="border p-2" />
      </label>
      <br />
      <button type="submit" className="bg-blue-500 text-white p-2">Sign Up</button>
    </form>
  );
};

export default Signup;